<template><section><h1>PolylineVolumeGeometry、 PolylineVolumeOutlineGeometry</h1>
<ul>
<li><code v-pre>vc-geometry-polyline-volume</code> 组件用于加载多段线柱几何体。</li>
<li><code v-pre>vc-geometry-outline-polyline-volume</code> 组件用于加载多段线柱几何体轮廓。</li>
<li>需要作为 <code v-pre>vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre>vc-primitive</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载多段线柱几何体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer ref="viewer" @LEFT_CLICK="LEFT_CLICK">
        <vc-primitive :appearance="appearance" :geometryInstances.sync="geometryInstances">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-polyline-volume ref="polylineVolume" :polylinePositions="positions" :shapePositions="shape" :vertexFormat="vertexFormat"></vc-geometry-polyline-volume>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-polyline-volume ref="polylineVolumeOutline" :polylinePositions="positionsOutline" :shapePositions="shape">
            </vc-geometry-outline-polyline-volume>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
